<script setup>
import { ref, onMounted, onUnmounted } from "vue";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { inject } from "vue";
import { sloganAnimateConfig } from "./data/data";
gsap.registerPlugin(ScrollTrigger);

// ---- 參數設定 -------------------------------------
const scrollValue = ref(0);
const navScrolled = ref(false);
const isMobile = ref(false);

// 右側按鈕滑動監控
const showButtons = ref(false);

const main = ref(null);
const ucg = ref(null);
const ucgMobile = ref(null);

// 年
const year = new Date().getFullYear();

// 主視覺動畫層
const mainSloganAnimateConfig = ref(sloganAnimateConfig);
// loading
const isRunMainAnimate = inject("isRunMainAnimateFlag");

// 滑動監控
const handleScroll = () => {
  scrollValue.value = main.value.scrollTop;
  navScrolled.value = scrollValue.value > 300;
  showButtons.value = scrollValue.value > 300;
};

const handleResize = () => {
  // 獲取當前螢幕寬度，若大於 768 則將變數 isMobile 改為 true
  if (window.innerWidth > 768) {
    isMobile.value = false;
  } else {
    isMobile.value = true;
  }
};

const mobileMenuOpen = ref(false);
const closeMenuAnimation = ref(false);
// Menu 跳窗
function openMobileMenu() {
  mobileMenuOpen.value = true;
  closeMenuAnimation.value = false;
}

function closeMobileMenu() {
  closeMenuAnimation.value = true;
  setTimeout(() => {
    mobileMenuOpen.value = false;
    closeMenuAnimation.value = false;
  }, 1000);
}

// 滑動
const scrollToMain = () => {
  if (main.value) {
    const topPos = 0;
    if (isMobile.value) {
      closeMenuAnimation.value = true;
      setTimeout(() => {
        mobileMenuOpen.value = false;
        closeMenuAnimation.value = false;
        main.value.scrollTo({
          top: topPos,
          behavior: "smooth",
        });
      }, 800);
    } else {
      main.value.scrollTo({
        top: topPos,
        behavior: "smooth",
      });
    }
  }
};

const navScrollToMain = () => {
  if (main.value) {
    const topPos = 0;
    main.value.scrollTo({
      top: topPos,
      behavior: "smooth",
    });
  }
};

const scrollToUCG = () => {
  if (main.value) {
    let topPos = ucg.value.getBoundingClientRect().top;
    if (isMobile.value) {
      topPos = ucgMobile.value.getBoundingClientRect().top;
      closeMenuAnimation.value = true;
      setTimeout(() => {
        mobileMenuOpen.value = false;
        closeMenuAnimation.value = false;
        main.value.scrollTo({
          top: topPos,
          behavior: "smooth",
        });
      }, 800);
    } else {
      main.value.scrollTo({
        top: topPos,
        behavior: "smooth",
      });
    }
  }
};

const initializePage = () => {
  logoMarqueeAction();
  handleResize();
  startAnimation();
};

onMounted(() => {
  initializePage();
  window.addEventListener("resize", handleResize);
});

onUnmounted(() => {
  window.removeEventListener("scroll", handleScroll);
  window.removeEventListener("resize", handleResize);
});

// ==== 主視覺動畫 =====================================
const startAnimation = () => {
  // 循環遍歷所有配置項
  mainSloganAnimateConfig.value.forEach((config, index) => {
    // 為每個元素設置一個延時，延時時間隨索引增加
    setTimeout(() => {
      const element = document.getElementById("sloganAnimate" + index);
      if (element) {
        // 將元素從隱藏改為顯示
        element.style.display = "block";

        // 如果是最後一個元素
        if (index === mainSloganAnimateConfig.value.length - 1) {
          // 等待0.3秒後關閉覆蓋層
          setTimeout(() => {
            document.getElementById("sloganOverlay").style.display = "none";
            isRunMainAnimate.value = true;
          }, 1000); // 這裡確保是在最後一個元素顯示後再等待0.3秒
        }
      }
    }, index * 300); // 每個元素之間等待0.3秒
  });
};

// ==== 過場跑馬燈動畫 ==============================
const logoMarquee = ref(null);
// 獲取跑馬燈容器的寬度

const logoMarqueeAction = () => {
  // 用 GASP 做出無縫的跑馬燈
  const animationDistance = logoMarquee.value.offsetWidth / 2;
  gsap.to(".logoMarquee", {
    x: -animationDistance,
    duration: isMobile ? 36 : 60,
    ease: "none",
    repeat: -1,
  });
};
</script>

<style>
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;500;700&display=swap");
@import url("../public/index.css");
/* animate.css */
@import url("https://cdnjs.cloudflare.com/ajax/libs/animate.css/4.1.1/animate.min.css");
</style>
